.detox-container {
    background-color: var(--secondary-color);
    width: 100%;
    padding-bottom: 50px;
}


.detox {
    background-color: var(--tertiary-color);
    display: flex;
    flex-direction: column;

}

.tips-detox {
    align-items: center;
    display: flex;
    flex-direction: column;
   
}

.titre-detox {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 30px;
    background-color: #4e342e91;
    width: 100%;
    margin: 0 auto; /* Centrer l'élément */
    margin-bottom: 50px;
    

}

.titre-detox2 {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 30px;
    background-color: #4e342e91;
    width: 100%;
    margin: 0 auto;
    margin-bottom: 50px;
    color: var(--secondary-color);

}

.titre-detox h1 {
    font-size: 40px;
    color: var(--secondary-color) ;
    font-weight: 200; 
}

.detox-product {
    display: flex;
    justify-content: center;
    flex-direction: row;
    margin: 0 auto;
    padding-top: 0 auto;
    gap: 20px;
    margin-left: 50px;
    margin-bottom: 50px;
    background-color: #4e342e18;
    width: 1400px;
}

.detox-product img {
    width: 300px;
    height: 270px;
    object-fit: cover;
}


.detail-detox-product {
    display: flex;
    flex-direction: column;
  
    gap: 20px;
    width: 650px;
}

.detail-detox-product h2 {
    color: var(--senary-color);
    font-weight: 700;
}

.p-detox {
    color: #3a001ebd;
    font-size: 18px;
    
    
}


.detail-detox {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    background-color: #a5b68dbb;
    padding: 10px;
    padding-left: 20px;
    padding-right: 20px;
    
}

.detail-detox img {
    width: 30px;
    height: 30px;
}

.detail-detox p {
    color:var(--primary-color);
    text-shadow: 0px 0px 10px var(--tertiary-color);
}

.tips-ingredient {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0px;
  
    margin-left: 40px;
    margin-right: 40px;
    
    
}

.timing-recette {
    color: var(--senary-color);
}


.tips-ingredient img {
    width: 40px;
    height: 40px;
    object-fit: cover;
    box-shadow: 0px 8px 0px var(--quaternary-color);
}

.tips-ingredient p {
    color: var(--secondary-color);
    font-weight: 700;
    background-color: #4e342e91;
    padding: 20px;
    border: 2px dotted var(--secondary-color);
    font-size: 15px;
}


.idea {
    width: 50px;
    height: 50px;
    object-fit: cover;
    margin-bottom: 30px;
    margin-top: 30px;
}